<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex md6>
        <v-card>
          <v-card-title>
            <v-layout row wrap>
              <v-flex xs11>
                <v-text-field
                  hide-details
                  solo
                  v-model="group.name"
                  @blur="pushUpdate()">
                </v-text-field>
              </v-flex>
              <v-flex xs1 class="text-xs-right">
                <v-menu>
                  <v-btn small flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
                  <v-list>
                    <v-list-tile @click="dialog.refresh.shown = true">
                      <v-list-tile-title><v-icon class="small indigo--text">fa-refresh</v-icon> Refresh All</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="dialog.reboot.shown = true">
                      <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot All</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="dialog.wake.shown = true">
                      <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up All</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="dialog.shutdown.shown = true">
                      <v-list-tile-title><v-icon class="small red--text">fa-plug</v-icon> Shutdown All</v-list-tile-title>
                    </v-list-tile>
                  </v-list>
                </v-menu>
              </v-flex>
            </v-layout>
          </v-card-title>
          <v-card-text>
            <group-settings :id="$route.params.id"></group-settings>
            <br /><br />
            <h3>Overclock Settings</h3>
            <oc-settings v-model="group.hardware.overclock" :changed="pushUpdate"></oc-settings>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6>
        <v-card>
          <v-card-text>
            <machines-table :group="this.$route.params.id" actions />
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>

    <v-dialog v-model="dialog.refresh.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to refresh all of the group's miners?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.refresh.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.refresh.shown = false; doGroupAction($route.params.id, 'refresh')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.wake.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to wake up all of the group's miners?<br />
          <small><b>Note:</b> This only works on machines with Wake-on-LAN enabled.</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.wake.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.wake.shown = false; doGroupAction($route.params.id, 'wake')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.reboot.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to reboot all of the group's miners?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.reboot.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.reboot.shown = false; doGroupAction($route.params.id, 'reboot')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.shutdown.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to shutdown all of the group's miners?<br />
          <small><b>Note:</b> You will have to turn them on manually if Wake-on-LAN is not enabled!</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.shutdown.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.shutdown.shown = false; doGroupAction($route.params.id, 'shutdown')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        dialog: {
          refresh: {
            shown: false
          },
          wake: {
            shown: false
          },
          reboot: {
            shown: false
          },
          shutdown: {
            shown: false
          }
        }
      }
    },
    computed: {
      group() {
        return this.$store.state.groups[this.$route.params.id];
      },
      machines() {
        let machines = {};
        for(let id in this.$store.state.machines) {
          if(this.$store.state.machines[id]['group'] == this.$route.params.id) {
            machines[id] = this.$store.state.machines[id];
          }
        }
        return machines;
      }
    },
    methods: {
      doAction(action) {
        let v = {to: 'one'}; v[this.$route.params.id] = {id: action};
        this.$socket.send('groups', 'action', v);
      },
      pushUpdate() {
        let vm = this;
        this.$nextTick(function() {
          let v = {}; v[vm.$route.params.id] = vm.$store.state.groups[vm.$route.params.id];
          this.$socket.send('groups', 'update', v);
        });
      }
    }
  }
</script>



