<template>
  <div>
    <v-app v-if="!$socket.ready">
      <v-card style="width:100%;max-width:250px;margin:80px auto 0">
        <v-card-text class="text-xs-center" style="font-size: 1.5em;font-weight:100">
          SecretWeb: <code>IVY</code>
        </v-card-text>
        <v-card-actions>
          <v-progress-linear :indeterminate="true"></v-progress-linear>
        </v-card-actions>
        <v-card-text class="text-xs-center">
          CONNECTING
        </v-card-text>
      </v-card>
    </v-app>
    <v-app v-else-if="!loaded">
      <v-card style="width:100%;max-width:300px;margin:80px auto 0">
        <v-card-text class="text-xs-center" style="font-size: 1.5em;font-weight:100">
          SecretWeb: <code>IVY</code>
        </v-card-text>
        <v-card-actions>
          <v-progress-linear :value="loadProgress / 8 * 100"></v-progress-linear>
        </v-card-actions>
        <v-card-text>
          <table style="width:100%">
            <tr>
              <td>LOADING WALLETS</td>
              <td class="text-xs-right"><v-icon v-html="wallets ? 'done' : 'clear'"></v-icon></td>
            </tr>
            <tr>
              <td>LOADING POOLS</td>
              <td class="text-xs-right"><v-icon v-html="pools ? 'done' : 'clear'"></v-icon></td>
            </tr>
            <tr>
              <td>LOADING GROUPS</td>
              <td class="text-xs-right"><v-icon v-html="groups ? 'done' : 'clear'"></v-icon></td>
            </tr>
            <tr>
              <td>LOADING MACHINES</td>
              <td class="text-xs-right"><v-icon v-html="machines ? 'done' : 'clear'"></v-icon></td>
            </tr>
          </table>
        </v-card-text>
      </v-card>
    </v-app>
    <v-app v-else>
      <v-navigation-drawer
        fixed
        clipped
        v-model="sidebar.shown"
        :mini-variant="$store.state.sidebar.mini"
        app>
        <v-list>
          <v-list-tile
            v-for="(item, i) in items"
            :key="i"
            :to="{name: item.name}"
            :exact="item.exact !== undefined ? item.exact : false"
            :active-class="item.color ? item.color + '--text' : ''">
            <v-list-tile-action>
              <v-icon v-html="item.icon"></v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title v-text="item.name"></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar fixed app clipped-left dark>
        <v-toolbar-side-icon @click.stop="sidebar.shown = !sidebar.shown"></v-toolbar-side-icon>
        <v-toolbar-title>Ivy</v-toolbar-title>
      </v-toolbar>
      <v-content>
        <v-container fluid>
          <v-slide-y-transition mode="out-in">
            <router-view></router-view>
          </v-slide-y-transition>
        </v-container>
      </v-content>
      <v-footer app>
        <span>&copy; 2018</span>
      </v-footer>
      <v-snackbar v-model="snackbar.shown">
        {{ snackbar.text }}
        <v-btn flat color="pink" @click.native="snackbar.shown = false">Close</v-btn>
      </v-snackbar>
    </v-app>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        items: [
          {color: 'light-blue', icon: 'dashboard', name: 'Overview', exact: true},
          {color: 'red', icon: 'favorite', name: 'Health Center'},
          {color: 'light-green', icon: 'layers', name: 'Groups'},
          {color: 'blue-grey', icon: 'dns', name: 'Miners'},
          {color: 'indigo', icon: 'fingerprint', name: 'Wallets'},
          {color: 'cyan', icon: 'location_on', name: 'Pools'},
          {color: 'black', icon: 'library_books', name: 'Software'},
          // {color: 'blue', icon: 'fa-tools', name: 'Tools'},
          // {color: 'blue', icon: 'fa-info', name: 'Health Check'},
          {color: 'grey', icon: 'settings', name: 'Settings'}
        ],

        sidebar: {
          shown: true
        },

        loop: null,
        loaded: true,

        snackbar: {
          shown: false,
          text: null
        }
      }
    },
    computed: {
      loadProgress() {
        let t = 0;
        if(this.wallets) t += 1;
        if(this.pools) t += 1;
        if(this.machines) t += 1;
        if(this.groups) t += 1;
        if(this.coins) t += 1;
        if(this.tickers) t += 1;
        if(this.software) t += 1;
        if(this.messages) t += 1;
        return t;
      },

      coins() {
        return this.$store.state.coins;
      }, tickers() {
        return this.$store.state.tickers;
      }, software() {
        return this.$store.state.software;
      }, fee() {
        return this.$store.state.fee;
      },

      messages() {
        return this.$store.state.messages;
      },

      wallets() {
        return this.$store.state.wallets;
      }, pools() {
        return this.$store.state.pools;
      }, machines() {
        return this.$store.state.machines;
      }, groups() {
        return this.$store.state.groups;
      }
    },
    methods: {
      initLoop() {
        let vm = this;
        this.cleanLoop();
        this.loop = setInterval(this.queryLoading, 5000);
      },
      cleanLoop() {
        if(this.loop != null) {
          clearInterval(this.loop);
          this.loop = null;
        }
      },
      queryLoading() {
        let vm = this;

        console.log("Requerying for data...");

        if(!vm.coins) {
          console.log('  ->coins...');
          vm.$socket.send('coins', 'get', {to: 'one'});
        }

        if(!vm.tickers) {
          console.log('  ->tickers...');
          vm.$socket.send('tickers', 'get', {to: 'one'});
        }

        if(!vm.software) {
          console.log('  ->software...');
          vm.$socket.send('software', 'get', {to: 'one'});
        }

        if(!vm.fee) {
          console.log('  ->fee...');
          vm.$socket.send('fee', 'get', {to: 'one'});
        }

        if(!vm.messages) {
          console.log('  ->messages...');
          vm.$socket.send('messages', 'get', {to: 'one'});
        }

        if(!vm.pools)
          setTimeout(function() {
            vm.$socket.send('pools', 'get', {to: 'one'});
          }, Math.random() * 1000 + 250);

        if(!vm.wallets)
          setTimeout(function() {
            vm.$socket.send('wallets', 'get', {to: 'one'});
          }, Math.random() * 1000 + 250);

        if(!vm.groups)
          setTimeout(function() {
            vm.$socket.send('groups', 'get', {to: 'one'});
          }, Math.random() * 1000 + 250);

        if(!vm.machines)
          setTimeout(function() {
            vm.$socket.send('machines', 'get', {to: 'one'});
          }, Math.random() * 1000 + 250);

        if(this.loadProgress == 8)
          this.loaded = true;
      }
    },
    watch: {
      '$socket.ready': {
        handler(val, oldVal) {
          if(!val) {
            console.error('Master socket connection closed!');

            this.$store.commit('RESET_DATA');
          }else if(val) {
            console.log('Established connection to master!');
            this.loaded = false;
          }
        }
      },
      loaded(val, oldVal) {
        this.cleanLoop();

        if(val) {
          console.log('Loading complete!');
        }else if(this.$socket.ready) {
          console.log('Attempting to load data...');
          this.queryLoading();
          this.initLoop();
        }
      },

      wallets(val, oldVal) {
        if(oldVal && val) {
          this.snackbar.text = 'Configured wallets updated!';
          this.snackbar.shown = true;
        }
      }, pools(val, oldVal) {
        if(oldVal && val) {
          this.snackbar.text = 'Configured pools updated!';
          this.snackbar.shown = true;
        }
      }, groups(val, oldVal) {
        if(oldVal && val) {
          this.snackbar.text = 'Configured groups updated!';
          this.snackbar.shown = true;
        }
      },
      messages(val, oldVal) {
        if(oldVal && val) {
          let numBadOld = 0;
          for(let i in oldVal) numBadOld += oldVal.level == 'bug' || oldVal.level == 'warning' || oldVal.level == 'danger';
          let numBadNew = 0;
          for(let i in val) numBadNew += val.level == 'bug' || val.level == 'warning' || val.level == 'danger';

          if(numBadNew > numBadOld) {
            this.snackbar.text = 'You have new notifications!';
            this.snackbar.shown = true;
          }
        }
      }
    }
  }
</script>



