<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex lg4>
        <v-card>
          <v-data-table
            :headers="pool_headers"
            :items="pools_data"
            hide-actions
            must-sort>
            <template slot="items" slot-scope="props">
              <tr :active="selected == props.item._id" @click="selected = props.item._id" style="cursor:pointer">
                <td>{{ props.item.name }}</td>
                <td><span v-if="props.item.endpoints">{{ Object.keys(props.item.endpoints).length }}</span><span v-else>0</span> endpoints</td>
                <td class="text-xs-right">
                  <v-btn small icon class="mx-0" @click="deletePool(props.item._id)">
                    <v-icon color="grey">close</v-icon>
                  </v-btn>
                </td>
              </tr>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <v-flex lg8>
        <v-card v-if="!pool">
          <v-card-title primary-title>
            <h1 class="headline mb-0">Pools</h1>
          </v-card-title>
          <v-card-text style="text-align:justify">
            <p>
              Your mining <b>Pool</b>s are grouped on the left, usually by pool name, with further configurations
              within it defining the endpoints available within the pool. The only required field is the <b>URL</b>, the rest are optional.
              The <b>Cryptocurrency</b> field is used to help calculate your <b>Daily Estimate</b>, and serves no other purpose.
              The <b>Stratum</b> setting prepends <code>stratum+tcp://</code> to your URL. This may be used for additional features in the future.
            </p>
            <p>
              <b>Note:</b> If the <b>Cryptocurrency</b> field is not set, the <b>Daily Estimate</b> is calculated using the, if defined, <b>Wallet</b> setting. If you set
              a <b>Wallet</b> in your <b>Group</b> configuration, the <b>Username</b> setting is overriden with the <b>Wallet Address</b>.
            </p>
          </v-card-text>
        </v-card>
        <v-card v-else>
          <v-card-title>
            <v-text-field
              label="Pool Name"
              single-line
              hide-details
              v-model="pools[selected].name"
              @blur="pushUpdate(selected)">
            </v-text-field>
            <v-spacer></v-spacer>
            <v-btn large outline color="primary" @click="dialog.shown = true">Add</v-btn>
            <v-text-field
              style="min-width:175px"
              append-icon="search"
              label="Search"
              solo
              single-line
              hide-details
              v-model="search"
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="endpoints_headers"
            :items="endpoints_data"
            must-sort
            :search="search">
            <template slot="items" slot-scope="props">
              <tr @click="dialog.pool_endpoint.data = Object.assign({}, props.item); dialog.pool_endpoint.edit = props.item._id; dialog.pool_endpoint.shown = true" style="cursor:pointer">
                <td class="text-xs-center">
                  <v-tooltip bottom>
                    <v-icon slot="activator" :color="props.item.stratum ? 'green' : 'grey'">check_circle</v-icon>
                    <span>{{ props.item.stratum ? 'Uses Stratum' : 'Doesn\'t use Stratum'}}</span>
                  </v-tooltip>
                </td>
                <td>{{ coins[props.item.crypto] ? coins[props.item.crypto].name : props.item.crypto }}</td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.url }}</td>
                <td>{{ props.item.user ? props.item.user : 'N/A' }}</td>
                <td>{{ props.item.pass ? props.item.pass : 'N/A' }}</td>
                <td class="text-xs-right">
                  <v-btn small icon class="mx-0" @click.stop="deletePoolConfig(selected, props.item._id)">
                    <v-icon class="grey--text">close</v-icon>
                  </v-btn>
                </td>
              </tr>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>

    <v-fab-transition>
      <v-speed-dial
        bottom
        right
        fixed
        direction="top"
        transition="slide-y-reverse-transition"
        v-show="fab.show"
        v-model="fab.dial">
        <v-btn dark fab hover color="primary darken-2" slot="activator" v-model="fab.dial">
          <v-icon>location_on</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
        <v-tooltip left>
          <v-btn fab dark small color="green" slot="activator" @click.stop="dialog.pool.shown = true">
            <v-icon>add</v-icon>
          </v-btn>
          <span>Create Pool</span>
        </v-tooltip>
        <v-tooltip left v-if="selected">
          <v-btn fab dark small color="indigo" slot="activator" @click.stop="dialog.pool_endpoint.shown = true">
            <v-icon>location_on</v-icon>
          </v-btn>
          <span>Create Pool Endpoint</span>
        </v-tooltip>
      </v-speed-dial>
    </v-fab-transition>

    <v-dialog v-model="dialog.pool.shown" max-width="500px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Create Pool</h4>
        </v-card-title>
        <v-card-text>
          <v-text-field
            label="Pool Name"
            clearable
            solo
            persistent-hint
            hint="This can be changed later"
            v-model="dialog.pool.name">
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.pool.shown = false">Close</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.pool.shown = false; createPool();">Create</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.pool_endpoint.shown" max-width="500px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">{{ dialog.pool_endpoint.edit ? 'Edit' : 'Create'}} Pool Endpoint</h4>
        </v-card-title>
        <v-card-text>
          <v-form v-model="dialog.pool_endpoint.valid" ref="form">
            <v-layout row wrap>
              <v-flex xs4 class="pr-1">
                <v-select
                  :items="coins_data"
                  item-value="_id"
                  item-text="name"
                  v-model="dialog.pool_endpoint.data.crypto"
                  label="Cryptocurrency"
                  single-line
                  bottom
                  autocomplete
                ></v-select>
              </v-flex>
              <v-flex xs8 class="pl-1">
                <v-text-field
                  label="Endpoint Name"
                  required
                  :rules="[v => !!v || 'Name is required']"
                  v-model="dialog.pool_endpoint.data.name">
                </v-text-field>
              </v-flex>
            </v-layout>
            <v-text-field
              label="URL"
              required
              :prefix="dialog.pool_endpoint.data.stratum ? 'stratum+tcp://' : ''"
              :rules="[v => !!v || 'URL is required']"
              v-model="dialog.pool_endpoint.data.url">
            </v-text-field>
            <v-layout row wrap>
              <v-flex xs6 class="pr-1">
                <v-text-field
                  label="Username"
                  persistent-hint
                  v-model="dialog.pool_endpoint.data.username">
                </v-text-field>
              </v-flex>
              <v-flex xs6 class="pl-1">
                <v-text-field
                  label="Password"
                  persistent-hint
                  v-model="dialog.pool_endpoint.data.password">
                </v-text-field>
              </v-flex>
            </v-layout>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-switch
            label="Stratum"
            v-model="dialog.pool_endpoint.data.stratum">
          </v-switch>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.pool_endpoint.shown = false">Close</v-btn>
          <v-btn color="primary" flat :disabled="!dialog.pool_endpoint.valid" @click.stop="dialog.pool_endpoint.shown = false; saveDialog();">{{ dialog.pool_endpoint.edit ? 'Save' : 'Create'}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        fab: {
          show: false,
          dial: false
        },
        dialog: {
          pool: {
            shown: false,
            name: null
          },
          pool_endpoint: {
            edit: null,
            shown: false,
            valid: true,

            data: {
              crypto: null,
              name: null,
              url: null,
              username: null,
              password: null,
              stratum: true
            }
          }
        },

        pool_headers: [
          { text: 'Name', value: 'name', align: 'left' },
          { text: 'Endpoints', value: 'endpoints', align: 'left', sortable: false },
          { text: 'Action', value: 'action', align: 'right', sortable: false }
        ],
        endpoints_headers: [
          { text: '', value: 'stratum', align: 'center' },
          { text: 'Crypto', value: 'crypto', align: 'left' },
          { text: 'Name', value: 'name', align: 'left' },
          { text: 'URL', value: 'url', align: 'left' },
          { text: 'User', value: 'user', align: 'left' },
          { text: 'Pass', value: 'pass', align: 'left' },
          { text: 'Action', value: 'action', align: 'right', sortable: false }
        ],

        selected: null,
        search: null
      }
    },
    computed: {
      coins() {
        return this.$store.state.coins;
      },
      coins_data() {
        let coins = [];
        for(let id in this.coins)
          coins.push(Object.assign({_id: id}, this.coins[id]));
        return coins;
      },

      pools() {
        return this.$store.state.pools;
      },
      pools_data() {
        let pools = [];
        for(let id in this.pools)
          pools.push(Object.assign({_id: id}, this.pools[id]));
        return pools;
      },

      pool() {
        if(!this.selected) return null;
        return this.pools[this.selected];
      },
      endpoints_data() {
        let endpoints = [];
        for(let id in this.pool.endpoints)
          endpoints.push(Object.assign({_id: id}, this.pool.endpoints[id]));
        return endpoints;
      }
    },
    watch: {
      'dialog.pool.shown': function(val, valOld) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.pool.name = null;
          }, 200);
        }
      },
      'dialog.pool_endpoint.shown': function(val, valOld) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.pool_endpoint.edit = null;
            vm.resetEndpointData();
          }, 200);
        }
      }
    },
    mounted() {
      let vm = this;
      setTimeout(() => { vm.fab.show = true }, 500);
    },
    methods: {
      saveDialog() {
        if(this.dialog.pool_endpoint.edit != null) {
          this.$store.state.pools[this.selected].endpoints[this.dialog.pool_endpoint.edit] = this.dialog.pool_endpoint.data;

          this.pushUpdate(this.selected);
        }else{
          if(!this.createNewEndpoint()) return;
        }
      },
      resetEndpointData() {
        this.dialog.pool_endpoint.data = {
          crypto: null,
          name: null,
          url: null,
          username: null,
          password: null,
          stratum: true
        };
      },
      pushUpdate(id) {
        this.$nextTick(function() {
          if(id === undefined)
            this.$socket.send('pools', 'update', this.$store.state.pools);
          else{
            let v = {}; v[id] = this.$store.state.pools[id];
            this.$socket.send('pools', 'update', v);
          }
        });
      },
      deletePool(id) {
        this.$socket.send('pools', 'delete', id);
      },
      createPool() {
        if(!this.dialog.pool.name) return;
        this.$socket.send('pools', 'new', {name: this.dialog.pool.name});
      },
      createNewEndpoint() {
        if(!this.dialog.pool_endpoint.valid) return false;
        this.$socket.send('pools', 'new_endpoint', {
          id: this.selected,
          crypto: this.dialog.pool_endpoint.data.crypto,
          name: this.dialog.pool_endpoint.data.name,
          url: this.dialog.pool_endpoint.data.url,
          username: this.dialog.pool_endpoint.data.username,
          password: this.dialog.pool_endpoint.data.password,
          stratum: this.dialog.pool_endpoint.data.stratum
        });
        return true;
      },
      deletePoolConfig(pool_id, id) {
        this.$socket.send('pools', 'delete_endpoint', {pool_id, id});
      },
    }
  }
</script>



