<template>
  <div>
    <v-data-table
      must-sort
      :select-all="selectable !== undefined"
      item-key="_id"
      :headers="headers"
      :items="machines_data"
      :search="search"
      v-model="selected"
    >
      <template slot="items" slot-scope="props">
        <router-link tag="tr" :to="{ name: 'Miner', params: {id: props.item._id} }" style="cursor:pointer">
          <td
            v-if="selectable !== undefined"
            @click.stop="">
            <v-checkbox
              primary
              hide-details
              v-model="props.selected"
            ></v-checkbox>
          </td>
          <td>
            <v-tooltip top v-if="!miner_stats[props.item._id] || !miner_stats[props.item._id].online">
              <v-icon class="error--text" slot="activator">label</v-icon>
              <span>Offline</span>
            </v-tooltip>
            <v-tooltip top v-else-if="miner_stats[props.item._id].online && props.item.dummy">
              <v-icon class="primary--text" slot="activator">label</v-icon>
              <span>Monitoring</span>
            </v-tooltip>
            <v-tooltip top v-else-if="miner_stats[props.item._id].online && !isSynced(props.item.group.id, props.item)">
              <v-icon class="warning--text" slot="activator">label</v-icon>
              <span>Not Synced</span>
            </v-tooltip>
            <v-tooltip top v-else>
              <v-icon class="success--text" slot="activator">label</v-icon>
              <span>Online</span>
            </v-tooltip>
          </td>
          <td>{{ props.item.name }}</td>
          <td
            v-if="group === undefined"
            @click.stop="">
            <v-select
              label="Group"
              solo
              flat
              autocomplete
              hide-details
              style="box-shadow:none"
              :items="groups_data"
              item-value="_id"
              item-text="name"
              v-model="props.item.group.id"
              @change="pushUpdate(props.item._id)">
            </v-select>
          </td>
          <td v-if="small === undefined">{{ convertTemperature(getMaxTemperature({miner: props.item._id}), true) }}</td>
          <td v-if="small === undefined">{{ convertRate(getTotalRate({miner: props.item._id}), true) }}/s</td>
          <td v-if="small === undefined">
            <div v-if="props.item.hardware && props.item.hardware.gpus">
              <span v-if="getOnlineGPUs({miner: props.item._id}) >= props.item.hardware.gpus.length">{{ getOnlineGPUs({miner: props.item._id}) }} / {{ props.item.hardware.gpus.length }}</span>
              <span v-else>{{ getOnlineGPUs({miner: props.item._id}) }} / {{ props.item.hardware.gpus.length }}</span>
            </div>
          </td>
          <td class="text-xs-right px-3"
            @click.stop="">
            <v-menu left>
              <v-btn small flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
              <v-list>
                <v-list-tile @click="doMinerAction(props.item._id, 'refresh')">
                  <v-list-tile-title><v-icon class="small indigo--text">fa-refresh</v-icon> Refresh</v-list-tile-title>
                </v-list-tile>
                <v-list-tile @click="doMinerAction(props.item._id, 'reboot')">
                  <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot</v-list-tile-title>
                </v-list-tile>
                <v-list-tile @click="doMinerAction(props.item._id, 'wake')" v-if="!miner_stats[props.item._id] || !miner_stats[props.item._id].online">
                  <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up</v-list-tile-title>
                </v-list-tile>
                <v-list-tile @click="doMinerAction(props.item._id, 'shutdown')" v-else>
                  <v-list-tile-title><v-icon class="small red--text">fa-plug</v-icon> Shutdown</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </td>
        </router-link>
      </template>
    </v-data-table>
  </div>
</template>

<script>
  export default {
    name: 'machines-table',
    props: ['search', 'group', 'selectable', 'small'],
    data() {
      let headers = [
        { text: '', value: 'online', align: 'left', sortable: false },
        { text: 'Name', value: 'name', align: 'left' }
      ];

      if(this.group === undefined)
        headers.push({ text: 'Group', value: 'group', align: 'left' });

      if(this.small === undefined) {
        headers.push({ text: 'Temperature', align: 'left', sortable: false })
        headers.push({ text: 'Rate', align: 'left', sortable: false })
        headers.push({ text: 'GPUs', align: 'left', sortable: false })
      }

      headers.push({ text: 'Actions', align: 'right', sortable: false })
      return {
        headers,
        selected: []
      }
    },
    computed: {
      groups() {
        return this.$store.state.groups;
      },
      groups_data() {
        let groups = [];
        for(let id in this.$store.state.groups)
          groups.push(Object.assign({_id: id}, this.groups[id]));
        return groups;
      },

      machines() {
        return this.$store.state.machines;
      },
      machines_data() {
        let machines = [];
        for(let id in this.$store.state.machines) {
          if(this.group !== undefined && this.machines[id].group.id != this.group) continue;
          machines.push(Object.assign({_id: id}, this.machines[id]));
        }
        return machines;
      },

      miner_stats() {
        return this.$store.state.miner_stats;
      }
    },
    methods: {
      pushUpdate(id) {
        this.$nextTick(function() {
          if(id === undefined)
            this.$socket.send('machines', 'update', this.$store.state.machines);
          else{
            let v = {}; v[id] = this.$store.state.machines[id];
            this.$socket.send('machines', 'update', v);
          }
        });
      }
    }
  }
</script>



