<template>
  <v-card>
    <v-card-title primary-title>
      <v-layout row wrap>
        <v-flex xs12 sm8>
          <h4 class="headline mb-0">{{ title }}<small>{{ total ? ': ' + total : '' }}</small></h4>
          <p>{{ info }}</p>
        </v-flex>
        <v-flex xs12 sm4 class="text-xs-center text-sm-right">
          <v-menu
            offset-y
            max-height="200px">
            <v-btn color="primary" dark slot="activator"><i class="fa fa-history"></i> {{ increments[increment][1] }}</v-btn>
            <v-list dense>
              <v-list-tile v-for="(data, i) in increments" :key="i" @click="increment = i; update()">
                <v-list-tile-title>{{ data[1] }}</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </v-flex>
      </v-layout>
    </v-card-title>
    <div v-if="state == 'loading'" class="text-xs-center pa-3">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </div>
    <div v-else-if="state == 'incomplete'" class="text-xs-center pa-3">
      Not enough data to display that time frame.
    </div>
    <div :style="'display:' + (state == 'done' ? 'block' : 'none')">
      <canvas :id="id" ref="canvas"></canvas>
    </div>
  </v-card>
</template>

<script>
  import { mixins, Line } from 'vue-chartjs'

  export default {
    extends: Line,
    mixins: [mixins.reactiveData],
    name: 'statistics',
    props: ['title', 'info', 'id', 'type'],
    data() {
      return {
        increment: (!localStorage['stats-' + this.id] ? (this.increment ? this.increment : 1) : localStorage['stats-' + this.id]),
        increments: [
          ['5m', '5 minutes'],
          ['10m', '10 minutes'],
          ['30m', '30 minutes'],
          ['1hr', 'Hourly'],
          ['6hr', '6 hours'],
          ['12hr', '12 hours'],
          ['1d', 'Daily'],
          ['7d', 'Weekly']
        ],

        state: 'loading',
        loop: null,

        total: null,

        label: '',
        chartData: {},
        chartOptions: {}
      }
    },
    computed: {
      statistics() {
        return this.$store.state.statistics[this.id];
      }
    },
    watch: {
      increment(val, oldVal) {
        localStorage['stats-' + this.id] = val;
        this.initLoop();
      },
      'statistics':  {
        handler(val, oldVal) {
          let labels = [];
          let series = [];

          let max_amt = [0, 'kH'];

          this.total = 0;

          for(let i in val) {
            if(i == val.length - 1) continue;

            let date = new Date(val[i][0].replace(/-/g, '/') + ' UTC');

            if(this.increment > 5)
             labels.push(date.toLocaleDateString([], { month: 'short', day: 'numeric' }));
            else
              labels.push(date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }));

            if(val[i][1] == null) {
              series.push(null);
              continue;
            }

            if(this.type == 'gpus') {
              series.push(val[i][1]['gpus'] > 0 ? val[i][1]['gpus'] : null);
              this.total += val[i][1]['gpus'];
            }else if(this.type == 'online') {
              series.push(val[i][1]['online'] > 0 ? val[i][1]['online'] : null);
              this.total += val[i][1]['online'];
            }else if(this.type == 'offline') {
              series.push(val[i][1]['offline'] > 0 ? val[i][1]['offline'] : null);
              this.total += val[i][1]['offline'];
            }else if(this.type == 'rate') {
              if(val[i][1]['rate'] > max_amt[0]) {
                max_amt = [val[i][1]['rate'], this.convertRate(val[i][1]['rate'])[1]]
              }

              series.push(val[i][1]['rate'] > 0 ? val[i][1]['rate'] : null);
            }

            else if(this.type == 'invalid') {
              series.push(val[i][1]['shares']['invalid'] > 0 ? val[i][1]['shares']['invalid'] : null);
              this.total += val[i][1]['shares']['invalid'];
            }else if(this.type == 'accepted') {
              series.push(val[i][1]['shares']['accepted'] > 0 ? val[i][1]['shares']['accepted'] : null);
              this.total += val[i][1]['shares']['accepted'];
            }else if(this.type == 'rejected') {
              series.push(val[i][1]['shares']['rejected'] > 0 ? val[i][1]['shares']['rejected'] : null);
              this.total += val[i][1]['shares']['rejected'];
            }
          }

          if(this.total == 0)
            this.total = null;

          if(this.type == 'rate') {
            this.label = ' ' + max_amt[1] + '/s';
            for(let i in series) {
              if(series[i] == null) continue;
              series[i] = this.convertRate(series[i], false, max_amt[1])[0].toFixed(2);
            }
          }

          this.chartData = {
            labels: labels,
            datasets: [
              {
                label: this.title,
                backgroundColor: "rgb(244, 67, 54, .8)",
                data: series
              }
            ]
          };

          this.state = series.length < 2 ? 'incomplete' : 'done';
        },
        deep: true
      }
    },
    methods: {
      initLoop() {
        let vm = this;

        if(this.loop != null)
          clearInterval(this.loop);

        this.loop = setInterval(function() {
          vm.update(true);
        }, 1000 * 60 * 2.5);
      },
      update(silent=false) {
        if(!silent) {
          this.state = 'loading';
          this.total = null;
        }

        this.$socket.send('stats', 'query', {
          'id': this.id,
          'increment': this.increments[this.increment][0]
        });
      }
    },
    mounted() {
      this.initLoop();

      this.update();

      this.renderChart(this.chartData, {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: false,
              callback: function(value, index, values) {
                return ((value * 100).toFixed(0) / 100) + this.label;
              }
            }
          }]
        }
      });
    },
    destroyed() {
      clearInterval(this.loop);
    }
  }
</script>



