<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md3>
                <v-icon class="success--text" style="font-size:4rem" v-if="getOfflineMiners() == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md9>
                <b>Miners Online</b>
                <v-divider class="my-2"></v-divider>
                {{ getOnlineMiners() }}/{{ getTotalMiners() }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md3>
                <v-icon class="success--text" style="font-size:4rem" v-if="getOfflineGPUs() == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md9>
                <b>GPUs Online</b>
                <v-divider class="my-2"></v-divider>
                {{ getOnlineGPUs() }}/{{ getTotalGPUs() }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md3>
                <v-icon class="success--text" style="font-size:4rem" v-if="machinesUnassigned == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md9>
                <b>Unassigned Miners</b>
                <v-divider class="my-2"></v-divider>
                {{ machinesUnassigned }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md3>
                <v-icon class="success--text" style="font-size:4rem" v-if="machinesUnsynced == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md9>
                <b>Unsynced Miners</b>
                <v-divider class="my-2"></v-divider>
                {{ machinesUnsynced }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12 md3>
        <v-card>
          <v-list dense>
            <v-list-tile  @click="filter = null">
              <v-list-tile-avatar>
                <v-icon>select_all</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>All</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <template v-for="(level, id) in levels">
              <v-list-tile :key="id" @click="filter = id">
                <v-list-tile-avatar>
                  <v-icon :class="level.color ? level.color + '--text' : ''">{{ level.icon }}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>{{ level.name }}</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
            </template>
          </v-list>
        </v-card>
      </v-flex>
      <v-flex xs12 md9>
        <v-card>
          <v-card-text>
            <center class="pa-3" v-if="messages == 0">No notifications to show. That'll probably change, soon.</center>
            <v-layout row wrap v-else v-for="[i, msg] in messages" :key="i">
              <v-flex xs2 sm1 class="text-xs-center">
                <v-icon v-if="msg.level" :class="levels[msg.level].color ? levels[msg.level].color + '--text' : ''">{{ levels[msg.level].icon }}</v-icon>
              </v-flex>
              <v-flex xs10 sm8 v-html="msg.text"></v-flex>
              <v-flex offset-xs3 offset-sm0 xs3 sm1 class="text-xs-center">
                <div v-if="msg.machine">
                  <router-link v-if="machines.hasOwnProperty(msg.machine)" :to="{ name: 'Machine', params: {id: msg.machine} }">
                    <v-btn icon ripple style="margin:0">
                      <v-icon class="blue-grey--text">dns</v-icon>
                    </v-btn>
                  </router-link>
                  <v-btn icon ripple disabled style="margin:0" v-else>
                    <v-icon class="blue-grey--text">dns</v-icon>
                  </v-btn>
                </div>
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <div v-if="msg.group">
                  <router-link v-if="groups.hasOwnProperty(msg.group)" :to="{ name: 'Group', params: {id: msg.group} }">
                    <v-btn icon ripple style="margin:0">
                      <v-icon class="light-green--text">layers</v-icon>
                    </v-btn>
                  </router-link>
                  <v-btn icon ripple disabled style="margin:0" v-else>
                    <v-icon class="light-green--text">layers</v-icon>
                  </v-btn>
                </div>
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <v-btn icon ripple style="margin:0" @click="deleteMessage(i)">
                  <v-icon class="grey--text">close</v-icon>
                </v-btn>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        filter: null,
        levels: {
          success: {
            name: 'Success',
            icon: 'check',
            color: 'success'
          },
          info: {
            name: 'Information',
            icon: 'info',
            color: 'info'
          },
          bug: {
            name: 'Bug',
            icon: 'bug_report'
          },
          caution: {
            name: 'Caution',
            icon: 'warning',
            color: 'yellow'
          },
          warning: {
            name: 'Warning',
            icon: 'error',
            color: 'orange'
          },
          danger: {
            name: 'Danger',
            icon: 'flash_on',
            color: 'error'
          }
        }
      }
    },
    computed: {
      messages() {
        let messages = [];
        for(let i in this.$store.state.messages) {
          let msg = this.$store.state.messages[i];
          if(this.machine && msg.machine != this.machine) continue;
          if(this.filter != null && this.filter != msg['level']) continue;
          messages.push([parseInt(i), msg]);
        }
        return messages;
      },

      groups() {
        return this.$store.state.groups;
      },

      machines() {
        return this.$store.state.machines;
      },

      machinesUnassigned() {
        let t = 0;
        for(let i in this.machines)
          t += this.machines[i].group.id ? 0 : 1;
        return t;
      },
      machinesUnsynced() {
        let t = 0;
        for(let i in this.machines) {
          if(!this.machines[i].group.id) continue;
          t += this.isSynced(this.machines[i].group.id, this.machines[i]) ? 0 : 1;
        }
        return t;
      }
    },
    methods: {
      lenLevel(level) {
        let t = 0;
        for(let msg of this.$store.state.messages)
          t += level == null || msg.level == level ? 1 : 0
        return t;
      },
      formatTime(time) {
        let date = new Date(time.replace(/-/g, '/') + ' UTC');
        return date.toLocaleDateString([], { month: 'short', day: 'numeric' });
      },
      deleteMessage(i) {
        this.$socket.send('messages', 'delete', i);
      }
    }
  }
</script>



