import Vue from 'vue'
import Router from 'vue-router'

import Overview from './pages/Overview'
import HealthCenter from './pages/HealthCenter'

import Groups from './pages/Groups'
import Group from './pages/Group'

import Machines from './pages/Machines'
import Machine from './pages/Machine'

import Wallets from './pages/Wallets'
import Pools from './pages/Pools'
import Software from './pages/Software'

import Settings from './pages/Settings'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'Overview', component: Overview },
    { path: '/health-center', name: 'Health Center', component: HealthCenter },

    { path: '/groups', name: 'Groups', component: Groups },
    { path: '/groups/:id', name: 'Group', component: Group },

    { path: '/machines', name: 'Miners', component: Machines },
    { path: '/machines/:id', name: 'Miner', component: Machine },

    { path: '/wallets', name: 'Wallets', component: Wallets },
    { path: '/pools', name: 'Pools', component: Pools },
    { path: '/software', name: 'Software', component: Software },
    { path: '/settings', name: 'Settings', component: Settings }
  ]
})



