<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 md5>
        <v-card>
          <v-data-table
            :headers="headers"
            :items="software_data"
            hide-actions>
            <template slot="headers" slot-scope="props">
              <tr>
                <th v-for="(type, id) in support_types" :key="id">{{ id }}</th>
                <th v-for="header in props.headers" :key="header.text" :class="'text-xs-' + header.align">
                  {{ header.text }}
                </th>
              </tr>
            </template>
            <template slot="items" slot-scope="props">
              <tr :active="selected == props.item._id" @click="selected = props.item._id" style="cursor:pointer">
                <td v-for="(type, id) in support_types" :key="id">
                  <v-icon slot="activator" color="success" v-if="props.item.support.indexOf(id) >= 0">check</v-icon>
                </td>
                <td>{{ props.item.name }}</td>
                <td>
                  <span v-if="props.item.suggested.length < 10">{{ props.item.suggested.join(', ') }}</span>
                  <span class="text-muted" v-else>{{ props.item.suggested.length + ' CRYPTOS...' }}</span>
                </td>
              </tr>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
      <v-flex xs12 md7>
        <v-card v-if="!selected">
          <v-card-title primary-title>
            <h1 class="headline mb-0">Software</h1>
          </v-card-title>
          <v-card-text style="text-align:justify">
            <p>
              This is the list of supported <b>Grizzly</b> software. Adding your own to the list is not currently supported, but if you would like
              one added, make sure to send us a feature request. Before using one of these in your group's configuration, be absolutely sure
              that the selected program supports the GPUs and drivers that are used.
            </p>
            <p>
              <b>Note:</b> This list of software is updated automatically once every few days from our local repo.
            </p>
          </v-card-text>
        </v-card>
        <v-card v-else>
          <v-card-title primary-title>
            <div>
              <h2 class="headline mb-0">{{ program.name }}</h2>
              <div>{{ program.url }}</div>
            </div>
          </v-card-title>
          <v-card-text style="text-align:justify">
            <v-card raised>
              <v-list dense three-line>
                <v-subheader>Command Template</v-subheader>
                <v-list-tile avatar>
                  <v-list-tile-content>
                    {{ program.execute.file + ' ' + program.execute.args }}
                  </v-list-tile-content>
                </v-list-tile>
                <div v-if="program.other">
                  <v-divider class="my-3" />
                  <v-subheader>Additional Information</v-subheader>
                  <v-list-tile avatar>
                    <v-list-tile-content>
                      {{ program.other }}
                    </v-list-tile-content>
                  </v-list-tile>
                </div>
              </v-list>
            </v-card>
            <v-layout row wrap class="mt-2">
              <v-flex xs6 class="pr-2">
                <v-card raised class="mb-2" v-if="program.algorithm.length > 0">
                  <v-list dense>
                    <v-subheader>Supported Algorithms</v-subheader>
                    <v-list-tile avatar v-for="(type, id) in program.algorithm" :key="id">
                      <v-list-tile-content>
                        {{ type }}
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                </v-card>
                <v-card raised v-if="program.suggested.length > 0">
                  <v-list dense>
                    <v-subheader>Supported Algorithms</v-subheader>
                    <v-list-tile avatar>
                      <v-list-tile-content>
                        {{ program.suggested.join(', ') }}
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                </v-card>
              </v-flex>
              <v-flex xs6 class="pl-2">
                <v-card raised v-if="program.support.length > 0">
                  <v-list dense>
                    <v-subheader>Supported Drivers</v-subheader>
                    <v-list-tile avatar v-for="(type, id) in program.support" :key="id">
                      <v-list-tile-content>
                        {{ type }}
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                </v-card>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        headers: [
          { text: 'Name', value: 'name', align: 'left' },
          { text: 'Suggested', align: 'left' }
        ],

        support_types: {'R': 'AMD R Series', 'RX': 'AMD RX Series', 'CUDA': 'Nvidia CUDA'},

        selected: null
      }
    },
    computed: {
      software() {
        return this.$store.state.software;
      },
      software_data() {
        let software = [];
        for(let id in this.$store.state.software)
          software.push(Object.assign({_id: id}, this.software[id]));
        return software;
      },

      program() {
        return this.software[this.selected];
      }
    }
  }
</script>



