import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    now: Date.now() / 1000,

    sidebar: {
      mini: localStorage.sidebarMini == 'true'
    },
    temperature: localStorage.temperature ? localStorage.temperature : 'K',
    currency: localStorage.currency ? localStorage.currency : 'BTC',

    coins: null,
    tickers: null,
    software: null,
    fee: null,

    messages: null,

    pools: null,
    wallets: null,

    groups: null,
    machines: null,
    miner_stats: {},

    statistics: {}
  },
  mutations: {
    NOW: (state, data) => {
      Vue.set(state, 'now', data);
    },

    // Local Storage
    SET_SIDEBAR_MINI: (state, data) => {
      Vue.set(state.sidebar, 'mini', data);
      localStorage.sidebarMini = data;
    },
    SET_TEMPERATURE: (state, data) => {
      Vue.set(state, 'temperature', data);
      localStorage.temperature = data;
    },
    SET_CURRENCY: (state, data) => {
      Vue.set(state, 'currency', data);
      localStorage.currency = data;
    },

    RESET_DATA: (state) => {
      Vue.set(state, 'coins',  null);
      Vue.set(state, 'tickers',  null);
      Vue.set(state, 'software',  null);
      Vue.set(state, 'fee',  null);

      Vue.set(state, 'messages',  null);

      Vue.set(state, 'pools',  null);
      Vue.set(state, 'wallets',  null);

      Vue.set(state, 'groups',  null);
      Vue.set(state, 'machines',  null);
    },

    // Loaded
    UPDATE_COINS: (state, data) => {
      Vue.set(state, 'coins', data);
    },
    UPDATE_TICKERS: (state, data) => {
      Vue.set(state, 'tickers', data);
    },
    UPDATE_SOFTWARE: (state, data) => {
      Vue.set(state, 'software', data);
    },
    UPDATE_FEE: (state, data) => {
      Vue.set(state, 'fee', data);
    },
    UPDATE_MESSAGES: (state, data) => {
      if(!data) data = [];
      Vue.set(state, 'messages', data);
    },

    UPDATE_POOLS: (state, data) => {
      if(!data) data = {};
      data = Object.keys(data).sort().reduce((r, k) => (r[k] = data[k], r), {});
      Vue.set(state, 'pools', data);
    },
    PATCH_POOLS: (state, data) => {
      if(!state.pools) return;

      if(!data) data = {};
      data = Object.keys(data).sort().reduce((r, k) => (r[k] = data[k], r), {});
      for(let id in data) Vue.set(state.pools, id, data[id]);
    },

    UPDATE_WALLETS: (state, data) => {
      if(!data) data = {};
      data = Object.keys(data).sort().reduce((r, k) => (r[k] = data[k], r), {});
      Vue.set(state, 'wallets', data);
    },
    PATCH_WALLETS: (state, data) => {
      if(!state.wallets) return;

      if(!data) data = {};
      data = Object.keys(data).sort().reduce((r, k) => (r[k] = data[k], r), {});
      for(let id in data) Vue.set(state.wallets, id, data[id]);
    },

    UPDATE_GROUPS: (state, data) => {
      if(!data) data = {};
      data = Object.keys(data).sort().reduce((r, k) => (r[k] = data[k], r), {});
      Vue.set(state, 'groups', data);
    },
    PATCH_GROUPS: (state, data) => {
      if(!state.groups) return;

      if(!data) data = {};
      data = Object.keys(data).sort().reduce((r, k) => (r[k] = data[k], r), {});
      for(let id in data) Vue.set(state.groups, id, data[id]);
    },

    UPDATE_MINERS: (state, data) => {
      if(!data) data = {};
      Vue.set(state, 'machines', data);
    },
    PATCH_MINERS: (state, data) => {
      if(!state.machines) return;

      if(!data) data = {};
      for(let id in data) Vue.set(state.machines, id, data[id]);
    },

    UPDATE_STATS: (state, data) => {
      for(let id in data) Vue.set(state.miner_stats, id, data[id]);
    },

    UPDATE_STATISTICS: (state, data) => {
      Vue.set(state.statistics, data.id, data.data);
    }
  }
});

setInterval(function () {
  store.commit('NOW', Date.now() / 1000);
}, 1000);

import socket from './socket'
socket.register_listener('coins', 'data', function(data) {
  store.commit('UPDATE_COINS', data);
})
socket.register_listener('tickers', 'data', function(data) {
  store.commit('UPDATE_TICKERS', data);
})
socket.register_listener('software', 'data', function(data) {
  store.commit('UPDATE_SOFTWARE', data);
})
socket.register_listener('fee', 'data', function(data) {
  store.commit('UPDATE_FEE', data);
})
socket.register_listener('messages', 'data', function(data) {
  store.commit('UPDATE_MESSAGES', data);
})

socket.register_listener('pools', 'data', function(data) { store.commit('UPDATE_POOLS', data); });
socket.register_listener('pools', 'patch', function(data) { store.commit('PATCH_POOLS', data); });

socket.register_listener('wallets', 'data', function(data) { store.commit('UPDATE_WALLETS', data); });
socket.register_listener('wallets', 'patch', function(data) { store.commit('PATCH_WALLETS', data); });

socket.register_listener('groups', 'data', function(data) { store.commit('UPDATE_GROUPS', data); });
socket.register_listener('groups', 'patch', function(data) { store.commit('PATCH_GROUPS', data); });

socket.register_listener('machines', 'data', function(data) { store.commit('UPDATE_MINERS', data); });
socket.register_listener('machines', 'patch', function(data) { store.commit('PATCH_MINERS', data); });

socket.register_listener('machines', 'stats', function(data) { store.commit('UPDATE_STATS', data); });
socket.register_listener('stats', 'response', function(data) {
  store.commit('UPDATE_STATISTICS', data);
});

export default store



