<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 md3>
        <v-card>
          <v-list dense>
            <v-list-tile @click="filter = null">
              <v-list-tile-content>
                <v-list-tile-title>All</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <template v-for="(amt, id) in cryptos">
              <v-list-tile :key="id" @click="filter = id">
                <v-list-tile-content v-html="coins[id] ? coins[id].name : id"></v-list-tile-content>
                <v-list-tile-content class="align-end">{{ amt }}</v-list-tile-content>
              </v-list-tile>
            </template>
          </v-list>
        </v-card>
      </v-flex>
      <v-flex xs12 md9>
        <v-card>
          <v-card-title>
            <v-layout row wrap>
              <v-flex xs12 sm6 class="text-xs-center text-sm-left">
                <v-btn medium outline color="primary" @click="dialog.shown = true">Add</v-btn>
              </v-flex>
              <v-flex xs12 sm6>
                <v-text-field
                  append-icon="search"
                  label="Search"
                  solo
                  single-line
                  hide-details
                  v-model="search"
                ></v-text-field>
              </v-flex>
            </v-layout>
          </v-card-title>
          <v-data-table
            must-sort
            item-key="_id"
            :headers="headers"
            :items="wallet_data"
            :search="search">
            <template slot="items" slot-scope="props">
              <tr @click="dialog.data = Object.assign({}, wallets[props.item._id]); dialog.edit = props.item._id; dialog.shown = true" style="cursor:pointer">
                <td>{{ coins[props.item.crypto] ? coins[props.item.crypto].name : props.item.crypto }}</td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.address }}</td>
                <td class="text-xs-right">
                  <v-btn small icon class="mx-0" @click.stop="del.shown = true; del.id = props.item._id;">
                    <v-icon color="grey">close</v-icon>
                  </v-btn>
                </td>
              </tr>
            </template>
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>

    <v-fab-transition>
      <v-btn dark fab bottom right fixed color="primary" v-show="fab" @click="dialog.shown = true">
        <v-icon>add</v-icon>
      </v-btn>
    </v-fab-transition>

    <v-dialog v-model="dialog.shown" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ (dialog.edit !== null ? 'Edit' : 'Create') + ' Wallet' }}</span>
        </v-card-title>
        <v-card-text>
          <v-form v-model="dialog.valid" ref="form">
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm4>
                  <v-select
                    :items="coins_data"
                    item-value="_id"
                    item-text="name"
                    v-model="dialog.data.crypto"
                    label="Cryptocurrency"
                    single-line
                    bottom
                    autocomplete
                  ></v-select>
                </v-flex>
                <v-flex xs12 sm8>
                  <v-text-field
                    label="Wallet Name"
                    required
                    :rules="[v => !!v || 'Name is required']"
                    v-model="dialog.data.name">
                  </v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field label="Wallet Address" v-model="dialog.data.address"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="dialog.shown = false">Close</v-btn>
          <v-btn color="blue darken-1" flat :disabled="!dialog.valid" @click="saveDialog(); dialog.shown = false">{{ dialog.edit !== null ? 'Save' : 'Create' }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="del.shown" persistent max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Delete Wallet</span>
        </v-card-title>
        <v-card-text>
          Are you sure you want to delete that wallet?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="del.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="del.shown = false; deleteWallet(del.id)">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        fab: false,

        headers: [
          { text: 'Crypto', value: 'crypto', align: 'left' },
          { text: 'Name', value: 'name', align: 'left' },
          { text: 'Address', value: 'address', align: 'left', sortable: false, },
          { text: 'Actions', align: 'right', sortable: false, }
        ],
        search: null,

        filter: null,
        dialog: {
          edit: null,
          valid: true,
          shown: false,
          data: { crypto: null, name: null, address: null }
        },

        del: {
          shown: false,
          id: null
        }
      }
    },
    mounted() {
      let vm = this;
      setTimeout(() => { vm.fab = true }, 500);
    },
    watch: {
      'dialog.shown': function(val, valOld) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.edit = null;
            vm.resetItemData();
          }, 200);
        }
      }
    },
    computed: {
      coins() {
        return this.$store.state.coins;
      },
      coins_data() {
        let coins = [];
        for(let id in this.$store.state.coins)
          coins.push(Object.assign({_id: id}, this.coins[id]));
        return coins;
      },

      wallets() {
        return this.$store.state.wallets;
      },
      wallet_data() {
        let wallets = [];
        for(let id in this.$store.state.wallets)
          wallets.push(Object.assign({_id: id}, this.wallets[id]));
        return wallets;
      },

      cryptos() {
        let cryptos = {};
        for(let wallet of this.wallet_data) {
          if(!wallet.crypto) continue;
          if(!cryptos.hasOwnProperty(wallet.crypto))
            cryptos[wallet.crypto] = 0;
          cryptos[wallet.crypto]++;
        }
        return cryptos;
      }
    },
    methods: {
      saveDialog() {
        if(this.dialog.edit !== null) {
          this.$store.state.wallets[this.dialog.edit] = this.dialog.data;

          this.pushUpdate(this.dialog.edit);
        }else{
          if(!this.createNewWallet()) return;
        }

        this.resetItemData();
      },
      resetItemData() {
        this.dialog.data = { crypto: null, name: null, address: null };
      },
      pushUpdate(id) {
        this.$nextTick(function() {
          if(id === undefined)
            this.$socket.send('wallets', 'update', this.$store.state.wallets);
          else{
            let v = {}; v[id] = this.$store.state.wallets[id];
            this.$socket.send('wallets', 'update', v);
          }
        });
      },
      createNewWallet() {
        if(!this.dialog.data.name && !this.dialog.data.address) return false;
        this.$socket.send('wallets', 'new', this.dialog.data);
        return true;
      },
      deleteWallet(id) {
        this.$socket.send('wallets', 'delete', id);
      }
    }
  }
</script>



