import Vue from 'vue'
import App from './App.vue'

import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'
import colors from 'vuetify/es5/util/colors'

import Statistics from './components/Statistics.vue'
import GroupSettings from './components/GroupSettings.vue'
import MachinesTable from './components/MachinesTable.vue'
import OverclockSettings from './components/OverclockSettings.vue'

Vue.use(Vuetify, {
  theme: { }
})

Vue.component(Statistics.name, Statistics);
Vue.component(GroupSettings.name, GroupSettings);
Vue.component(MachinesTable.name, MachinesTable);
Vue.component(OverclockSettings.name, OverclockSettings);

import Socket from './socket'
Vue.use(Socket);

import './css/style.css'
import './utils'
import router from './router'
import store from './store'

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



