<template>
  <div>
    <v-layout row wrap v-if="value.core !== undefined">
      <v-flex xs6 class="pr-1">
        <v-text-field
          type="number"
          min="0"
          step="1"
          placeholder="0"
          persistent-hint
          hint="Value is in Mhz. E.x. 1200 = 1.2 Ghz"
          label="Core Mhz"
          v-model.number="value.core.mhz"
          @change="changed()">
        </v-text-field>
      </v-flex>
      <v-flex xs6 class="pl-1">
        <v-text-field
          type="number"
          min="0"
          step="1"
          placeholder="0"
          persistent-hint
          hint="Value is in millivolts. E.x. 800 = .8 volts"
          label="Core Voltage"
          v-model.number="value.core.vlt"
          @change="changed()">
        </v-text-field>
      </v-flex>
    </v-layout>
    <br />
    <v-layout row wrap v-if="value.mem !== undefined">
      <v-flex xs6 class="pr-1">
        <v-text-field
          type="number"
          min="0"
          step="1"
          placeholder="0"
          persistent-hint
          hint="Value is in Mhz. E.x. 1200 = 1.2 Ghz"
          label="Memory Mhz"
          v-model.number="value.mem.mhz"
          @change="changed()">
        </v-text-field>
      </v-flex>
      <v-flex xs6 class="pl-1">
        <v-text-field
          type="number"
          min="0"
          step="1"
          placeholder="0"
          persistent-hint
          hint="Value is in millivolts. E.x. 800 = .8 volts"
          label="Memory Voltage"
          v-model.number="value.mem.vlt"
          @change="changed()">
        </v-text-field>
      </v-flex>
    </v-layout>
    <br />
    <v-layout row wrap>
      <v-flex xs6 sm4 class="pr-1">
        <v-text-field
          type="number"
          min="0"
          step="1"
          hide-details
          placeholder="0"
          label="Power Limit"
          v-model.number="value.pwr"
          @change="changed()">
        </v-text-field>
      </v-flex>
      <v-flex xs6 sm4 class="px-1">
        <v-text-field
          type="number"
          min="0"
          step="0.1"
          hide-details
          placeholder="0"
          label="Maximum Temperature"
          v-model.number="value.temp.max"
          @change="changed()">
        </v-text-field>
      </v-flex>
      <v-flex sm4 class="pl-1">
        <v-text-field
          type="number"
          min="0"
          max="100"
          step="0.1"
          hide-details
          placeholder="0"
          label="Minimum Fan Speed"
          v-model.number="value.fan.min"
          @change="changed()">
        </v-text-field>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
  export default {
    name: 'oc-settings',
    props: ['value', 'changed']
  }
</script>



