if(!window['WebSocket']) {
  console.error('Websockets not supported.');
}

let events = {};

function register_listener(id, method, func) {
  if(!events[id]) events[id] = {}
  if(!events[id][method]) events[id][method] = []
  events[id][method].push(func)
}

function connect(Vue, socket) {
  if(socket.connecting) return;
  if(socket.ready) socket.conn.close();

  Vue.set(socket, 'connecting', true);

  setTimeout(function() {
    console.log('Attempting to connect to ws://' + window.master.host + ':29203/...');

    socket.conn = new WebSocket('ws://' + window.master.host + ':29203/?subscribe={"*": ["data", "patch"], "machines": ["stats"]}');

    socket.send = function(event, method, data=undefined) {
      let to = undefined;
      if(data && data['to']) {
        to = data['to'];
        delete data['to'];

        if(data.length == 0)
          data = undefined;
      }
      socket.conn.send(JSON.stringify({event: event, method: method, to: to, payload: data}));
    };

    socket.conn.onopen = function() {
      Vue.set(socket, 'connecting', false);
      Vue.set(socket, 'ready', true);
    };

    socket.conn.onerror = function() {
      Vue.set(socket, 'connecting', false);
      connect(Vue, socket);
    };

    socket.conn.onclose = function() {
      Vue.set(socket, 'ready', false);
      connect(Vue, socket);
    };

    socket.conn.onmessage = function(e) {
       let data = JSON.parse(e.data);
       let event = data['event'];
       let method = data['method'];

       if(events[event]) {
         if(events[event][method]) {
           for(let i in events[event][method]) {
             events[event][method][i](data.payload);
           }
         }
       }
     };
   }, 5000);
}

export default {
  register_listener,
  install(Vue, options) {
    let socket = { connecting: false, ready: false };
    Vue.prototype.$socket = socket;

    window.master = {
      host: location.href.split('/')[2].split(':')[0],
      priority: Number.MAX_SAFE_INTEGER
    };

    var url = new URL(location.href);
    if(url.searchParams.get('master'))
      window.master.host = url.searchParams.get('master');

    window.master.receivedBroadcast = function(ip, packet) {
      for(let service of packet.services) {
        if(service.id == 'MASTER') {
          if(!socket.conn || service.payload.priority < master.priority) {
            master.host = ip;
            master.priority = service.payload.priority;

            // Close the socket. The script should attempt to reconnect to the host automatically.
            if(socket.conn)
              socket.conn.close();
          }
        }
      }
    };

    Vue.mixin({
      data() {
        return {
          '$socket': socket
        }
      }
    });

    connect(Vue, socket);
  }
};



