<template>
  <div>
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs12 offset-sm6 sm6>
            <v-text-field
              append-icon="search"
              label="Search"
              solo
              single-line
              hide-details
              v-model="search"
            ></v-text-field>
          </v-flex>
        </v-layout>
      </v-card-title>
      <machines-table :search="search" :selectable="undefined" />
    </v-card>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        search: null
      }
    }
  }
</script>



