<template>
  <div>
    <v-container fluid grid-list-md>
      <v-layout row wrap>
        <v-flex md6 lg3>
          <v-card>
            <v-card-text>
              <v-layout row wrap>
                <v-flex md3>
                  <v-icon color="black" style="font-size:4rem">fa-tachometer</v-icon>
                </v-flex>
                <v-flex md9>
                  <b>Hashrate</b>
                  <v-divider class="my-2"></v-divider>
                  {{ convertRate(getTotalRate(), true) }}/s
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md6 lg3>
          <v-card>
            <v-card-text>
              <v-layout row wrap>
                <v-flex md3>
                  <v-icon color="green" style="font-size:4rem">fa-money</v-icon>
                </v-flex>
                <v-flex md9>
                  <b>Daily Estimate</b>
                  <v-divider class="my-2"></v-divider>
                  {{ dailyEstimate }}
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md6 lg3>
          <v-card>
            <v-card-text>
              <v-layout row wrap>
                <v-flex md3>
                  <v-icon color="blue-grey" style="font-size:4rem">fa-database</v-icon>
                </v-flex>
                <v-flex md9>
                  <b>Miners</b>
                  <v-divider class="my-2"></v-divider>
                  {{ getOnlineMiners() }}/{{ getTotalMiners() }}
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md6 lg3>
          <v-card>
            <v-card-text>
              <v-layout row wrap>
                <v-flex md3>
                  <v-icon class="success--text" style="font-size:4rem" v-if="badOnes == 0">check</v-icon>
                  <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
                </v-flex>
                <v-flex md9>
                  <b>Health</b>
                  <v-divider class="my-2"></v-divider>
                  {{ badOnes }} Warnings
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex md7>
          <statistics
              title="Rate"
              info="The total hashrate of your farm"
              id="overview-rate" type="rate"></statistics>
        </v-flex>
        <v-flex md5>
          <statistics
            class="mb-2"
            title="Accepted Shares"
            info="The number of shares accepted over time"
            id="overview-accepted" type="accepted"></statistics>
          <statistics
            class="mt-2"
            title="Rejected Shares"
            info="The number of shares rejected over time"
            id="overview-rejected" type="rejected"></statistics>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  export default {
    computed: {
      coins() {
        return this.$store.state.coins;
      },
      tickers() {
        return this.$store.state.tickers;
      },
      messages() {
        return this.$store.state.messages;
      },

      wallets() {
        return this.$store.state.wallets;
      },

      groups() {
        return this.$store.state.groups;
      },
      pools() {
        return this.$store.state.pools;
      },
      machines() {
        return this.$store.state.machines;
      },
      miner_stats() {
        return this.$store.state.miner_stats;
      },

      badOnes() {
        let t = 0;
        for(let i in this.messages)
          t += this.messages[i].level == 'bug' || this.messages[i].level == 'warning' || this.messages[i].level == 'danger';
        return t;
      },

      dailyEstimate() {
        let earningsBTC = 0; // Per minute

        for(let j in this.machines) {
          let group = this.groups[this.machines[j].group.id];
          if(!group) continue;

          if(!group.pool || !this.pools.hasOwnProperty(group.pool.id)) continue;

          let pool = this.pools[group.pool.id].endpoints[group.pool.endpoint];
          if(!pool) continue;

          let wallet = this.wallets[group.wallet];

          let coin = this.coins[wallet && wallet.crypto ? wallet.crypto : pool.crypto]
          if(!coin) continue;

          let rate = 0;
          if(!this.miner_stats[j]) continue;
          for(let k in this.miner_stats[j].hardware.gpus) {
            rate += this.miner_stats[j].hardware.gpus[k].rate;
          }

          let blockTime = parseInt(coin.block_time);
          let netHash = parseInt(coin.nethash) / 1000;
          let ethPerMin = (60 / blockTime) * coin.block_reward;

          let userRatio = rate / netHash;

          earningsBTC += (userRatio * ethPerMin) * coin.exchange_rate;
        }

        if(this.tickers[localStorage.currency]) {
          return ((earningsBTC * 60 * 24) * this.tickers[localStorage.currency].sell).toFixed(2) + ' ' + localStorage.currency;
        }else
          return (earningsBTC * 60 * 24).toFixed(8) + ' BTC';
      }
    }
  }
</script>



