<template>
  <div>
    <v-layout row wrap>
      <v-flex xs6 sm4 class="pr-1">
        <v-select
          autocomplete
          clearable
          label="Pool"
          item-text="name"
          item-value="_id"
          :items="pools_data"
          v-model="group.pool.id"
          @change="pushUpdate()">
        </v-select>
      </v-flex>

      <v-flex xs6 sm4 class="px-1">
        <v-select
          autocomplete
          clearable
          :disabled="!group.pool || !pools.hasOwnProperty(group.pool.id)"
          label="Endpoint"
          item-text="name"
          item-value="_id"
          :items="pool_endpoints_data"
          v-model="group.pool.endpoint"
          @change="pushUpdate()">
        </v-select>
      </v-flex>

      <v-flex sm4 class="pl-1">
        <v-select
          autocomplete
          clearable
          :label="'Wallet (' + (isValidConfig && this.pools[this.group.pool.id].endpoints[this.group.pool.endpoint].username ? 'optional' : 'required') + ')'"
          item-text="name"
          item-value="_id"
          :items="wallets_data"
          v-model="group.wallet"
          @change="pushUpdate()">
        </v-select>
      </v-flex>
    </v-layout>
    <br />
    <v-layout row wrap>
      <v-flex sm4 class="pr-1">
        <v-select
          autocomplete
          clearable
          label="Mining Software"
          item-text="name"
          item-value="_id"
          :items="software_data"
          v-model="group.program.id"
          @change="changedProgram()">
        </v-select>
      </v-flex>
      <v-flex sm8 class="pl-1">
        <div v-if="software.hasOwnProperty(group.program.id) && group.program.execute">
          <v-text-field
            multi-line
            label="Command Template"
            style="resize:vertical"
            v-model="group.program.execute.args"
            @blur="pushUpdate()">
          </v-text-field>
        </div>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="software.hasOwnProperty(group.program.id)">
      <v-flex sm12>
        <v-card
          raised>
          <v-card-text>
            <div v-html="realGroupExec"></div>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<style>
  .label {
    padding: 2px 5px;

    color: #FFF;
    font-weight: 600;

    border-radius: 4px;
  }
</style>

<script>
  export default {
    name: 'group-settings',
    props: ['id'],
    computed: {
      group() {
        return this.$store.state.groups[this.id];
      },

      coins() {
        return this.$store.state.coins;
      },

      software() {
        return this.$store.state.software;
      },
      software_data() {
        let software = [];
        for(let id in this.software) {
          if(this.groupCrypto && this.software[id].algorithm.indexOf(this.coins[this.groupCrypto].algorithm) == -1) continue;
          software.push(Object.assign({_id: id}, this.software[id]));
        }
        return software;
      },

      pools() {
        return this.$store.state.pools;
      },
      pools_data() {
        let pools = [];
        for(let id in this.pools)
          pools.push(Object.assign({_id: id}, this.pools[id]));
        return pools;
      },
      pool_endpoints_data() {
        let endpoints = [];
        if(this.pools.hasOwnProperty(this.group.pool.id))
          for(let id in this.pools[this.group.pool.id].endpoints)
            endpoints.push(Object.assign({_id: id}, this.pools[this.group.pool.id].endpoints[id]));
        return endpoints;
      },

      wallets() {
        return this.$store.state.wallets;
      },
      wallets_data() {
        let wallets = [];
        for(let id in this.wallets) {
          if(this.groupCrypto && this.groupCrypto != this.wallets[id].crypto) continue;
          wallets.push(Object.assign({_id: id}, this.wallets[id]));
        }
        return wallets;
      },

      program() {
        return this.software[this.group.program.id];
      },
      groupCrypto() {
        let crypto = null;

        if(this.group.pool && this.group.pool.id && this.group.pool.endpoint) {
          if(this.pools.hasOwnProperty(this.group.pool.id)) {
            if(this.pools[this.group.pool.id].endpoints.hasOwnProperty(this.group.pool.endpoint)) {
              crypto = this.pools[this.group.pool.id].endpoints[this.group.pool.endpoint].crypto;
            }
          }
        }

        if(!crypto && this.group.wallet) {
          if(this.wallets.hasOwnProperty(this.group.wallet)) {
            crypto = this.wallets[this.group.wallet].crypto;
          }
        }

        return crypto;
      },

      isValidConfig() {
        return this.group.pool && this.group.pool.id && this.group.pool.endpoint && this.pools.hasOwnProperty(this.group.pool.id) && this.pools[this.group.pool.id].endpoints.hasOwnProperty(this.group.pool.endpoint);
      },

      realGroupExec() {
        if(!this.group.program.execute || !this.group.program.execute.args) return '';

        let exec = this.group.program.execute.args;

        exec = exec.replace(/(-[^-\s]+) ([^\s{]+)/g, '$1 <span class="label orange">$2</span>');
        exec = exec.replace(/(-[^-\s]+) {miner\.id}/g, '$1 <span class="label green">Miner ID</span>');

        if(this.group.wallet) {
          let cfg = this.wallets[this.group.wallet];
          if(cfg) {
            exec = exec.replace(/(-[^-\s]+) {user}/g, '$1 <span class="label blue">' + cfg['address'] + '</span>');
          }
        }

        if(this.isValidConfig) {
          let cfg = this.pools[this.group.pool.id].endpoints[this.group.pool.endpoint];
          if(cfg) {
            exec = exec.replace(/(-[^-\s]+) {pool\.url}/g, '$1 <span class="label blue">' + ((cfg['stratum'] ? 'stratum+tcp://' : '') + cfg['url']) + '</span>');
            exec = exec.replace(/(-[^-\s]+) {pool\.pass}/g, (!cfg['user'] ? '' : '$1 <span class="label blue">' + cfg['password'] + '</span>'));

            exec = exec.replace(/(-[^-\s]+) {user}/g, (!cfg['user'] ? '' : '$1 <span class="label blue">' + cfg['username'] + '</span>'));
          }
        }

        if(this.group.hardware && this.group.hardware.overclock) {
          let cfg = this.group.hardware.overclock;
          exec = exec.replace(/(-[^-\s]+) {core\.mhz}/g, (!cfg['core']['mhz'] ? '' : '$1 <span class="label blue">' + cfg['core']['mhz'] + '</span>'));
          exec = exec.replace(/(-[^-\s]+) {core\.vlt}/g, (!cfg['core']['vlt'] ? '' : '$1 <span class="label blue">' + cfg['core']['vlt'] + '</span>'));

          exec = exec.replace(/(-[^-\s]+) {mem\.mhz}/g, (!cfg['mem']['mhz'] ? '' : '$1 <span class="label blue">' + cfg['mem']['mhz'] + '</span>'));
          exec = exec.replace(/(-[^-\s]+) {mem\.vlt}/g, (!cfg['mem']['vlt'] ? '' : '$1 <span class="label blue">' + cfg['mem']['vlt'] + '</span>'));

          exec = exec.replace(/(-[^-\s]+) {pwr}/g, (!cfg['pwr'] ? '' : '$1 <span class="label blue">' + cfg['pwr'] + '</span>'));
          exec = exec.replace(/(-[^-\s]+) {temp.max}/g, (!cfg['temp']['max'] ? '' : '$1 <span class="label blue">' + cfg['temp']['max'] + '</span>'));
          exec = exec.replace(/(-[^-\s]+) {fan.min}/g, (!cfg['fan']['min'] ? '' : '$1 <span class="label blue">' + cfg['fan']['min'] + '</span>'));
        }

        exec = exec.replace(/{miner\.id}/g, '<span class="label green">Miner ID</span>');

        return '<span class="text-muted">' + this.program.execute.file + '</span> ' + exec;
      }
    },
    methods: {
      pushUpdate() {
        this.$nextTick(function() {
          let v = {}; v[this.id] = this.group;
          this.$socket.send('groups', 'update', v);
        });
      },

      changedProgram() {
        this.group.program.execute = {args: this.program.execute.args };
        this.pushUpdate();
      }
    }
  }
</script>



