<template>
  <v-layout row wrap>
    <v-flex xs12 md7 class="pa-2">
      <v-card>
        <v-card-title>
          <v-layout row wrap>
            <v-flex xs1>
              <v-tooltip top v-if="!miner_stats || !miner_stats.online">
                <v-icon large class="error--text" slot="activator">label</v-icon>
                <span>Offline</span>
              </v-tooltip>
              <v-tooltip top v-else-if="miner_stats.online && miner.dummy">
                <v-icon large class="primary--text" slot="activator">label</v-icon>
                <span>Monitoring</span>
              </v-tooltip>
              <v-tooltip top v-else-if="miner_stats.online && !isSynced(miner.group.id, miner)">
                <v-icon large class="warning--text" slot="activator">label</v-icon>
                <span>Not Synced</span>
              </v-tooltip>
              <v-tooltip top v-else>
                <v-icon large class="success--text" slot="activator">label</v-icon>
                <span>Online</span>
              </v-tooltip>
            </v-flex>
            <v-flex xs10>
              <v-text-field
                style="padding:0"
                flat
                hide-details
                single-line
                v-model="miner.name"
                @blur="pushUpdate()">
              </v-text-field>
            </v-flex>
            <v-flex xs1 class="text-xs-right">
              <v-menu left>
                <v-btn small flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
                <v-list>
                  <v-list-tile>
                    <v-list-tile-title class="grey--text text-xs-center">{{ miner.hardware.mac }}</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.reboot.id = id; dialog.reboot.shown = true">
                    <v-list-tile-title><v-icon class="small indigo--text">fa-refresh</v-icon> Refresh</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.reboot.id = id; dialog.reboot.shown = true">
                    <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.wake.id = id; dialog.wake.shown = true">
                    <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="dialog.shutdown.id = id; dialog.shutdown.shown = true">
                    <v-list-tile-title><v-icon class="small red--text">fa-power-off</v-icon> Shutdown</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </v-flex>
          </v-layout>
        </v-card-title>
        <v-card-text>
          <v-text-field
            style="margin-top:-30px"
            textarea
            hide-details
            label="Notes"
            v-model="miner.notes"
            @blur="pushUpdate()">
          </v-text-field>
        </v-card-text>
      </v-card>
      <v-card class="mt-3">
        <v-card-title>
          <h4 class="headline mb-0">Specs and Storage</h4>
        </v-card-title>
        <v-layout row wrap>
          <v-flex md6 class="pa-3" v-for="(cpu, i) in miner.hardware.cpus" :key="'cpu-' + i">
            <v-card raised>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex xs3>
                    <v-icon x-large>fa-cogs</v-icon>
                  </v-flex>
                  <v-flex xs9>
                    Processor<br />
                    <small>{{ cpu.product }}</small>
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </v-flex>
          <v-flex md6 class="pa-3" v-for="(memory, i) in miner.hardware.memory" :key="'mem-' + i">
            <v-card raised>
              <v-card-text>
                <v-layout row wrap>
                  <v-flex xs3>
                    <v-icon x-large>fa-tasks</v-icon>
                  </v-flex>
                  <v-flex xs9>
                    Memory<br />
                    <small>{{ (memory.size / 1024 / 1024 / 1024).toFixed(2) }} GB</small>
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
        <table class="table">
          <thead>
            <tr>
              <th class="text-xs-left">Type</th>
              <th class="text-xs-left">>Mount Point</th>
              <th style="width:120px">Usage</th>
              <th style="width:120px">Size</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(storage, i) in miner.hardware.storage">
              <td>{{ storage.fstype }}</td>
              <td>{{ storage.mount }}</td>
              <td>{{ (storage.space.used / 1024 / 1024 / 1024).toFixed(2) }} GB</td>
              <td>{{ (storage.space.total / 1024 / 1024 / 1024).toFixed(2) }} GB</td>
            </tr>
          </tbody>
        </table>
      </v-card>
    </v-flex>
    <v-flex xs12 md5 class="pa-2">
      <v-card>
        <v-list two-line>
          <v-subheader>
            GPUs
          </v-subheader>
          <template v-for="(gpu, i) in miner.hardware.gpus">
            <v-divider v-if="i > 0"></v-divider>
            <v-list-tile class="py-3" :key="'gpu-' + i">
              <v-list-tile-content>
                <v-container grid-list-xs>
                  <v-layout row wrap>
                    <v-flex xs1>
                      <div v-if="miner_stats && miner_stats.hardware.gpus[i]">
                        <v-icon class="success--text" v-if="miner_stats.hardware.gpus[i].online">label</v-icon>
                        <v-icon class="error--text" v-else>label</v-icon>
                      </div>
                      <v-icon class="grey--text" v-else>label</v-icon>
                    </v-flex>
                    <v-flex xs8>
                      {{ gpu.product }}
                    </v-flex>
                    <v-flex xs3 class="text-xs-right">
                      <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ convertRate(miner_stats.hardware.gpus[i].rate, true) }}/s</div>
                      <div v-else>N/A</div>
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap class="text-xs-center">
                    <v-flex xs6 md3>
                      <v-icon class="small" color="grey">fa-clock-o</v-icon>
                      <div>{{ gpu.clock / 100000 }} Mhz</div>
                    </v-flex>
                    <v-flex xs6 md3>
                      <v-icon class="small" color="blue-grey">fa-leaf</v-icon>
                      <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ miner_stats.hardware.gpus[i].fan }}%</div>
                      <div v-else> N/A</div>
                    </v-flex>
                    <v-flex xs6 md3>
                      <v-icon class="small" color="orange">fa-thermometer-empty</v-icon>
                      <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ convertTemperature(miner_stats.hardware.gpus[i].temp, true) }}</div>
                      <div v-else> N/A</div>
                    </v-flex>
                    <v-flex xs6 md3>
                      <v-icon class="small" color="orange">fa-bolt</v-icon>
                      <div v-if="miner_stats && miner_stats.hardware.gpus[i]">{{ miner_stats.hardware.gpus[i].watts }} W</div>
                      <div v-else> N/A</div>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  export default {
    computed: {
      messages() {
        return this.$store.state.messages;
      },
      miner() {
        return this.$store.state.machines[this.$route.params.id];
      },
      miner_stats() {
        return this.$store.state.miner_stats[this.$route.params.id];
      }
    },
    methods: {
      doAction(action) {
        let v = {to: 'one'}; v[this.$route.params.id] = {id: action};
        this.$socket.send('machines', 'action', v);
        this.$notify((action == 'refresh' ? 'info' : (action == 'reboot' ? 'warning' : 'error')), 'Instructing miner to ' + action + '...');
      },
      pushUpdate() {
        let vm = this;
        this.$nextTick(function() {
          let v = {}; v[this.$route.params.id] = vm.$store.state.machines[this.$route.params.id];
          this.$socket.send('machines', 'update', v);
        });
      }
    }
  }
</script>



